import { Pie } from 'vue-chartjs';
import * as echarts from "echarts";
import { ref, reactive, toRefs, watch, onMounted } from "vue";
import { ElBreadcrumb, ElBreadcrumbItem } from 'element-plus';
import { useRoute } from 'vue-router';
export default {
  components: {},
  setup() {
    const route = useRoute();
    const pathList = ref();
    const getCurrentPath = () => {
      pathList.value = route.matched.filter(item => item.meta && item.meta.title);
    };
    onMounted(() => {
      getCurrentPath();
    });
    watch(() => route, (to, from) => {
      pathList.value = to.matched.filter(item => item.meta && item.meta.title);
      console.log(to);
      console.log(from);
    }, {
      immediate: true
    });
    return {
      pathList
    };
  },
  data() {
    return {
      myChart: {},
      pieData: [{
        value: 1845,
        name: "萌芽期"
      }, {
        value: 1895,
        name: "发展期"
      }, {
        value: 1910,
        name: "成熟期"
      }],
      pieName: [],
      myChartStyle: {
        float: "left",
        width: "100%",
        height: "300px"
      },
      //图表样式
      showInfoBox: false,
      // 控制信息框显示隐藏
      selectedInfo: "",
      // 选中的信息
      selectedInfoImage: null,
      //坐标点的位置
      markers: [
      // { top: 530, left: 670, info: "檐廊", image: require("@/assets/jin/03.jpg") },
      // { top: 250, left: 650, info: "石库门顶", image: require("@/assets/hai/04.jpg") },
      {
        top: 250,
        left: 920,
        info: "外滩",
        image: require("@/assets/hai/02.png")
      }, {
        top: 450,
        left: 1210,
        info: "花园洋房",
        image: require("@/assets/hai/03.jpg")
      }, {
        top: 350,
        left: 650,
        info: "石库门",
        image: require("@/assets/hai/01.png")
      }],
      infoBoxTop: 0,
      // 信息框距离顶部的位置
      infoBoxLeft: 0 // 信息框距离左侧的位置
    };
  },
  mounted() {
    this.initDate(); //数据初始化
    this.initEcharts();
  },
  created() {
    this.updateLocalTime();
  },
  methods: {
    updateLocalTime() {
      // 获取本地时间并格式化
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, '0');
      const minutes = now.getMinutes().toString().padStart(2, '0');
      const seconds = now.getSeconds().toString().padStart(2, '0');
      this.localTime = `${hours}:${minutes}:${seconds}`;

      // 每秒更新一次时间
      setTimeout(this.updateLocalTime, 1000);
    },
    initDate() {
      for (let i = 0; i < this.pieData.length; i++) {
        // this.xData[i] = i;
        // this.yData =this.xData[i]*this.xData[i];
        this.pieName[i] = this.pieData[i].name;
      }
    },
    initEcharts() {
      // 饼图
      const option = {
        legend: {
          // 图例
          data: this.pieName,
          right: "10%",
          top: "30%",
          orient: "vertical"
        },
        title: {
          // 设置饼图标题，位置设为顶部居中
          text: "发展阶段",
          top: "5%",
          left: "center"
        },
        color: ['#c89b40', '#ae7000', '#b35c44'],
        series: [{
          type: "pie",
          label: {
            show: true,
            formatter: "{b} : {c} ({d}%)" // b代表名称，c代表对应值，d代表百分比
          },
          radius: "40%",
          //饼图半径
          data: this.pieData
        }]
      };
      // console.log(this.seriesData);
      const optionFree = {
        xAxis: {},
        yAxis: {},
        series: [{
          data: this.seriesData,
          type: "line",
          smooth: true
        }]
      };
      this.myChart = echarts.init(document.getElementById("mychart"));
      this.myChart.setOption(option);
      //随着屏幕大小调节图表
      window.addEventListener("resize", () => {
        this.myChart.resize();
      });
    },
    checkMarker(event) {
      // 检查点击位置是否在标记点附近
      const x = event.offsetX;
      const y = event.offsetY;
      for (const marker of this.markers) {
        if (Math.abs(marker.top - y) < 10 && Math.abs(marker.left - x) < 10) {
          // 如果点击位置在标记点附近，则显示相应的信息框
          this.showInfo(marker);
          break;
        } else {
          this.selectedInfo = null;
          this.showInfoBox = false; // 显示信息框
          this.selectedInfoImage = null;
        }
      }
    },
    showInfo(marker) {
      this.showInfoBox = true; // 显示信息框
      this.selectedInfo = marker.info;
      this.selectedInfoImage = marker.image;
      this.infoBoxTop = marker.top; // 设置信息框距离顶部的位置
      this.infoBoxLeft = marker.left + 20; // 设置信息框距离左侧的位置
    }
  }
};