    let $http = inject("axios");
    const route = useRoute();
    const pathList = ref([]);
    const getCurrentPath = () => {
      pathList.value = route.matched.filter(item => item.meta && item.meta.title);
    };
    onMounted(() => {
      getCurrentPath();
    });
    watch(() => route, (to, from) => {
      pathList.value = to.matched.filter(item => item.meta && item.meta.title);
      console.log(to);
      console.log(from);
    }, {
      immediate: true
    });
    const particlesInit = async engine => {
      //await loadFull(engine);
      await loadSlim(engine);
    };
    const handleLogin = () => {
      localStorage.setItem("token", "yhx");
    };
    // 粒子库 options 配置
    const options = {
      background: {
        color: {
          // value: '#d4c283'
        }
      },
      fpsLimit: 120,
      interactivity: {
        events: {
          onClick: {
            enable: true,
            mode: 'push'
          },
          onHover: {
            enable: true,
            mode: 'repulse'
          },
          resize: true
        },
        modes: {
          bubble: {
            distance: 400,
            duration: 2,
            opacity: 0.8,
            size: 40
          },
          push: {
            quantity: 4
          },
          repulse: {
            distance: 200,
            duration: 0.4
          }
        }
      },
      particles: {
        color: {
          // value: '#ffffff'
        },
        links: {
          // color: '#ffffff',
          distance: 150,
          enable: true,
          opacity: 0.1,
          width: 0
        },
        collisions: {
          enable: true
        },
        move: {
          direction: 'none',
          enable: true,
          outModes: {
            default: 'bounce'
          },
          random: false,
          speed: 1,
          straight: false
        },
        number: {
          density: {
            enable: true,
            area: 800
          },
          value: 100
        },
        opacity: {
          value: 0.3
        },
        shape: {
          // type: 'circle
          type: 'polygon',
          sides: 3 // 6个边形成六边形'
